.listItem {
	margin-bottom: 8px;
    border-radius: 4px;
    border: 1px solid var(--border-color-1);
    padding: 16px;
    cursor: pointer;
	background: var(--bg-white);
}
.tag {
	border-radius: 4px;
	font-size: 12px;
	line-height: 24px;
	text-align: center;
	margin: auto 0;
	width: 64px;
	color: #ffac4a;
	background-color: rgba(255, 172, 74, 0.1);
}
.tagCash {
	color: #1093F5ff !important;
	background-color: #1093F51A;
}
.tagCard {
	color: #1AAEC7ff !important;
	background-color: #1AAEC71A;
}
.tagCrypto {
	color: #5C33E6ff !important;
	background-color: #5C33E61A;
}